@charset 'utf-8';

@import url('https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap');

* { font-family: 'Nanum Myeongjo', serif; margin: 0; padding: 0; text-decoration: none; list-style: none; box-sizing: border-box; }
a { color: black; cursor: pointer; }
  
// header
header {
    background-color: beige;

    .header-wrap {
        max-width: 1200px;
        min-height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: auto;

        .menu-bar { display: none; }

        nav ul {
            display: flex;
            gap: 40px;

            li { cursor: pointer; transition: .3s; }
            li:hover { color: rgb(212, 136, 149); }
        }
    }
}
// header end

// main
main {
    .slide-wrap {
        display: flex;
        position: relative;
        overflow: hidden;
        max-width: 1200px;
        margin: auto;

        .slide-box {

            width: 100%;
            display: flex;
            transition: .4s;

            .slide {
                width: 100%;
                min-height: 500px;
                flex-shrink: 0;
                background-color: blanchedalmond;
                transition: 0s !important;

                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        button {
            width: 50px;
            height: 50px;
            color : #fff;
            background-color: rgb(129, 98, 51);
            border: 1px solid #fff;
            border-radius: 50%;
            cursor: pointer;
            transition: .3s;

            &:hover {
                color : rgb(129, 98, 51);
                background-color: #fff;
            }
        }

        .prev {
            position: absolute;
            top: 200px;
            left: 20px;
            z-index: 1;
        }

        .next { 
            position: absolute;
            top: 200px;
            right: 20px;
            z-index: 1;
        }
    }

    .gallery {
        
        overflow: hidden;

        .gallery-wrap {
            max-width: 1200px;
            margin: auto;
            padding: 100px 0;

            .card-wrap {
                display: flex;
                flex-wrap: wrap;
                gap: 10px;
                padding-top: 50px;

                .card {
                    flex-basis: calc(1180px / 3);
                    // flex-grow: 1;
                    flex-shrink: 1;
                    min-height: 400px;
                    border: 1px solid rgba(129, 98, 51, 0.3);
                    border-radius: 10px;
                    padding: 20px;
                    position: relative;
                    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.2);
                    background-color: rgb(245, 239, 231);
                    transition: .3s;

                    &:hover {
                        transform: rotate(-5deg);
                    }
                    
                    .img-box {
                        width: 100%;
                        height: 300px;
                        text-align: center;
                        margin: 15px 0;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }

                    span {
                        position: absolute;
                        top: 10px;
                        left: 20px;
                    }
                }
            }
        }
    }
}
// main end

// footer
footer {
    min-height: 100px;
}
// footer end

@media screen and (min-width: 721px) and (max-width: 1024px) {
    header {
        .header-wrap {
            .logo { transform: translateX(calc(50vw - 16px)); }
            .menu-bar { display: block; }
            nav { display: none; }
        }
    }

    main {
        .gallery {
            .gallery-wrap {
                .card-wrap {
                    .card {
                        flex-basis: calc((100% - 10px) / 2);
                    }
                }
            }
            
        }
    }
    
}

@media screen and (max-width: 720px) {
    main {
        .gallery {
            .gallery-wrap {
                .card-wrap {

                    gap: 30px;

                    .card {
                        flex-basis: 100%;

                        &:hover {
                            transform: translateY(-10px);
                        }
                    }
                }
            }
            
        }
    }
}